<template>
  <div id="app">
    <link rel="stylesheet" href="https://unpkg.com/element-ui/lib/theme-chalk/index.css" />
    <div class="header_container">
      <div class="app_title">
        <img src="https://www.downloadclipart.net/large/9405-music-icon-design.png" alt />
        <h1>Music Player</h1>
      </div>
      <el-autocomplete
        popper-class="my-autocomplete"
        v-model="state"
        :fetch-suggestions="querySearchAsync"
        :placeholder="placeholder"
        @select="handleSelect"
        :trigger-on-focus="false"
      >
        <template slot-scope="{ item }">
          <span class="value">{{ item.title }}</span>
        </template>
      </el-autocomplete>
    </div>
    <div class="body_container">
      <div v-for="(song, index) in songs" :key="index">
        <div class="song_play" v-on:click="playSong(song)">
          <h2>{{ song.title }}</h2>
          <img
            :id="`visible_play_animation${index}`"
            src="./assets/play_icon.gif"
            alt
            style="display: none"
          />
        </div>
      </div>
    </div>
    <div class="bottom_play_div">
      <div class="bottom_play_song" v-if="playSongVisible">
        <div class="bottom_play_songleft">
          <h2>
            <i>Title:</i>
            {{ currSong.title }}
          </h2>
          <h2>
            <i>Artists:</i>
            {{ currSong.artist }}
          </h2>
        </div>
        
        <div class="images">
          <img v-if="playSongVar" src="./assets/play_icon.gif" alt class="bottom_animation_icon" />
          <img
            src="https://www.shareicon.net/data/512x512/2016/04/10/747449_arrows_512x512.png"
            alt
            v-on:click="playBackSong"
          />
          <img
            src="http://icons.iconarchive.com/icons/mazenl77/I-like-buttons/64/Aqua-Pause-icon.png"
            alt
            v-if="playSongVar"
            v-on:click="pauseCurrSong"
          />
          <img
            src="https://www.freepngimg.com/thumb/button/63144-play-icons-button-youtube-computer-icon.png"
            alt
            v-else
            v-on:click="playSong(currSong)"
          />
          <img
            src="https://cdn4.iconfinder.com/data/icons/flat-circle-content/800/circle-content-play-512.png"
            alt
            v-on:click="playNextSong"
          />
        </div>
        <div class="bottom_play_song_right" v-on:click="cancelSong">
          <img src="https://freesvg.org/img/mcol_cross.png" alt />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "App",

  data() {
    return {
      links: [],
      state: "",
      timeout: null,
      inputValue: "",
      newPlayList: [],
      counter: 0,
      currSong: "",
      playSongVisible: false,
      playSongVar: false,
      placeholder: "🎻 Search By Song Name or Singer Name ♬",
      songs: [
        {
          title: "Teri Jhalak Asharfi",
          artist: "Javed Ali",
          src: require("./assets/Teri Jhalak Asharfi.mp3")
        },
        {
          title: "Main Duniya Bhula Doonga",
          artist: "Nadeem Akhtar Saifi, Shravan Kumar Rathod",
          src: require("./assets/Main Duniya Bhula Doonga.mp3")
        },
        {
          title: "Mere Sapnon Ki Rani",
          artist: "Kishore Kumar",
          src: require("./assets/Mere Sapnon Ki Rani.mp3")
        },
        {
          title: "Kyon Ki Itna Pyaar Tumse",
          artist: "Alka Yagnik, Udit Narayan",
          src: require("./assets/Kyon Ki Itna Pyaar Tumse.mp3")
        },
        {
          title: "Yaaron Sab Dua Karo",
          artist: "Stebin Ben & Danish Sabri",
          src: require("./assets/Yaaron Sab Dua Karo - Stebin Ben.mp3")
        },
        {
          title: "Dil tod ke",
          artist: "B Praak",
          src: require("./assets/Dil_Tod_Ke_1.mp3")
        },
        {
          title: "Mujho saja de",
          artist: "Yasser Desai",
          src: require("./assets/AUD-20220523-WA0000.mp3")
        },
        {
          title: "O Mere Dil Ke Chain",
          artist: "Kishore Kumar",
          src: require("./assets/O Mere Dil Ke Chain - Mere Jeevan Saathi 128 Kbps.mp3")
        },
        {
          title: "Rab Se Tujhe Maanga Kare",
          artist: "Darpan Shah",
          src: require("./assets/Rab Se Tujhe Maanga Kare (EDM Version)(PaglaSongs).mp3")
        },
        {
          title: "Teri Aadat Pai Gayi Aa",
          artist: "Ninja",
          src: require("./assets/Teri Aadat Pai Gayi Aa(Mr-Jatt1.com).mp3")
        },
        {
          title: "Choti-Choti-Gaiya-Chote-Chote-Gwal",
          artist: "Studio Sangeeta",
          src: require("./assets/Choti-Choti-Gaiya-Chote-Chote-Gwal.mp3")
        },
        {
          title: "Hare Krishna Hare Rama",
          artist: "Devangi Das, Manoj Sargam",
          src: require("./assets/Hare Krishna Hare Rama.mp3")
        }
      ],
      player: new Audio()
    };
  },
  mounted() {
    this.links = this.loadAll();
  },
  methods: {
    loadAll() {
      return this.songs;
    },
    querySearchAsync(queryString, cb) {
      var links = this.links;
      var results = queryString
        ? links.filter(this.createFilter(queryString))
        : links;
      clearTimeout(this.timeout);
      this.timeout = setTimeout(() => {
        cb(results);
      }, 3000 * Math.random());
    },
    createFilter(queryString) {
      return link => {
        return (
          link.title.toLowerCase().indexOf(queryString.toLowerCase()) !== -1 ||
          link.artist.toLowerCase().indexOf(queryString.toLowerCase()) !== -1
        );
      };
    },
    handleSelect(item) {
      this.playSong(item);
    },
    playBackSong() {
      document.getElementById(
        `visible_play_animation${this.counter}`
      ).style.display = "none";
      if (this.counter > 0) {
        this.counter -= 1;
        document.getElementById(
          `visible_play_animation${this.counter}`
        ).style.display = "block";
      } else {
        this.counter = this.songs.length - 1;
        document.getElementById(
          `visible_play_animation${this.counter}`
        ).style.display = "block";
      }
      this.player.src = this.songs[this.counter].src;
      this.currSong = this.songs[this.counter];
      this.player.play();
    },
    playNextSong() {
      document.getElementById(
        `visible_play_animation${this.counter}`
      ).style.display = "none";
      if (this.counter + 1 > this.songs.length - 1) {
        this.counter = 0;
        document.getElementById(
          `visible_play_animation${this.counter}`
        ).style.display = "block";
      } else {
        this.counter += 1;
        document.getElementById(
          `visible_play_animation${this.counter}`
        ).style.display = "block";
      }
      this.player.src = this.songs[this.counter].src;
      this.currSong = this.songs[this.counter];
      this.player.play();
    },
    playSong(song) {
      document.getElementById(
        `visible_play_animation${this.counter}`
      ).style.display = "none";
      this.currSong = song;
      this.player.src = song.src;
      this.player.play();
      this.playSongVisible = true;
      this.playSongVar = true;
      this.counter = 0;
      for (song of this.songs) {
        if (this.currSong.title === song.title) {
          document.getElementById(
            `visible_play_animation${this.counter}`
          ).style.display = "block";
          return false;
        }
        this.counter += 1;
      }
    },
    pauseCurrSong() {
      this.player.src = this.currSong.src;
      this.player.pause();
      document.getElementById(
        `visible_play_animation${this.counter}`
      ).style.display = "none";
      this.playSongVar = false;
    },
    cancelSong() {
      this.player.pause();
      this.playSongVisible = false;
      this.playSongVar = false;
      document.getElementById(
        `visible_play_animation${this.counter}`
      ).style.display = "none";
    }
  }
};
</script>

